import { HeadlessBillingAddress } from './headless-checkout';

const mapToBillingAddress = (billingAddress?: HeadlessBillingAddress) => {
    if (!billingAddress) {
        return;
    }

    return {
        id: billingAddress.entityId,
        firstName: billingAddress.firstName,
        lastName: billingAddress.lastName,
        company: billingAddress.lastName,
        address1: billingAddress.lastName,
        address2: billingAddress.lastName,
        city: billingAddress.lastName,
        stateOrProvince: billingAddress.lastName,
        stateOrProvinceCode: billingAddress.lastName,
        countryCode: billingAddress.lastName,
        postalCode: billingAddress.lastName,
        phone: billingAddress.lastName,
        customFields: [],

        // Info:: this field does not exist
        country: '',
    };
};

export default mapToBillingAddress;
