import Customer from '../customer';

import HeadlessCustomerResponse from './headless-customer';

export default function mapToCustomer(
    headlessCustomerResponse: HeadlessCustomerResponse,
): Customer {
    const { customer } = headlessCustomerResponse;

    return {
        id: customer?.entityId || 0,
        storeCredit: customer?.storeCredit[0]?.value || 0,
        email: customer?.email || '',
        firstName: customer?.firstName || '',
        lastName: customer?.lastName || '',
        fullName:
            customer?.firstName && customer?.lastName
                ? `${customer.firstName} ${customer.lastName}`
                : '',
        addresses:
            customer?.addresses.edges.map(({ node }) => ({
                address1: node.address1,
                address2: node.address2,
                city: node.city,
                company: node.company,
                countryCode: node.countryCode,
                id: node.entityId,
                firstName: node.firstName,
                lastName: node.lastName,
                phone: node.phone,
                postalCode: node.postalCode,
                stateOrProvince: node.stateOrProvince,
                customFields: [], // existing, need to check

                // Info:: fields above are not exist
                country: '',
                type: '',
                stateOrProvinceCode: '',
            })) || [],

        // Info:: fields above are not exist
        isGuest: true,
        shouldEncourageSignIn: false,
    };
}
