import { HeadlessShippingConsignment } from './headless-checkout';
import mapToShippingOption from './map-to-shipping-option';

const mapToConsignment = (shippingConsignments: HeadlessShippingConsignment[]) => {
    return shippingConsignments.map(
        ({
            address,
            entityId,
            handlingCost,
            lineItemIds,
            shippingCost,
            availableShippingOptions,
            selectedShippingOption,
        }) => {
            const headlessShippingAddress = {
                firstName: address.firstName,
                lastName: address.lastName,
                company: address.lastName,
                address1: address.lastName,
                address2: address.lastName,
                city: address.lastName,
                stateOrProvince: address.lastName,
                stateOrProvinceCode: address.lastName,
                countryCode: address.lastName,
                postalCode: address.lastName,
                phone: address.lastName,
                customFields: [],

                // Info:: this field does not exist
                country: '',
            };

            return {
                id: entityId,
                address: headlessShippingAddress,
                shippingAddress: headlessShippingAddress,
                handlingCost: handlingCost.value,
                lineItemIds,
                shippingCost: shippingCost.value,
                availableShippingOptions: availableShippingOptions.map(mapToShippingOption),
                selectedShippingOption: mapToShippingOption(selectedShippingOption),

                // Info:: this field does not exist
                selectedPickupOption: undefined,
            };
        },
    );
};

export default mapToConsignment;
