import HeadlessCheckoutResponse, { HeadlessCheckout } from './headless-checkout';
import mapToBillingAddress from './map-to-billing-address';
import mapToConsignment from './map-to-consignment';

export default function mapToCheckout(
    headlessCartResponse: HeadlessCheckoutResponse,
): HeadlessCheckout {
    const { checkout } = headlessCartResponse;

    const {
        billingAddress,
        customerMessage,
        shippingConsignments,
        taxes,
        giftWrappingCostTotal,
        shippingCostTotal,
        handlingCostTotal,
        taxTotal,
        subtotal,
        grandTotal,
        outstandingBalance,
        promotions,
        createdAt,
        updatedAt,
    } = checkout;

    return {
        id: checkout.entityId,
        billingAddress: mapToBillingAddress(billingAddress),
        customerMessage,
        consignments: mapToConsignment(shippingConsignments),
        taxes: taxes.map(({ name, amount }) => ({
            name,
            amount: amount.value,
        })),
        coupons: checkout.coupons.map((coupon) => ({
            id: coupon.entityId,
            code: coupon.code,
            couponType: coupon.couponType,
            discountedAmount: coupon.discountedAmount.value,

            displayName: '',
        })),
        giftWrappingCostTotal: giftWrappingCostTotal.value,
        shippingCostTotal: shippingCostTotal.value,
        handlingCostTotal: handlingCostTotal.value,
        taxTotal: taxTotal.value,
        subtotal: subtotal.value,
        grandTotal: grandTotal.value,
        outstandingBalance: outstandingBalance.value,
        promotions: promotions.map(({ banners }) => ({
            banners: banners.map((banner) => ({
                type: banner.type,
                text: banner.text,
            })),
        })),
        createdTime: createdAt.utc,
        updatedTime: updatedAt.utc,

        fees: [], // required but does not exist
        channelId: 0, // required but does not exist
        balanceDue: 0, // required but does not exist
        giftCertificates: [], // required but does not exist
        shouldExecuteSpamCheck: false, // required but does not exist
        shippingCostBeforeDiscount: 0, // required but does not exist
        isStoreCreditApplied: true, // phase 2 for applepay
        discounts: [], // required but does not exist
        // payments: ..., // optional but does not exist
        // orderId: 0, // optional but does not exist
    };
}
