import ShippingOption from '../../shipping/shipping-option';

import { HeadlessSelectedShippingOptions } from './headless-checkout';

const mapToShippingOption = (option: HeadlessSelectedShippingOptions): ShippingOption => ({
    description: option.description,
    id: option.entityId,
    imageUrl: option.imageUrl,
    cost: option.cost.value,
    transitTime: option.transitTime,
    type: option.type,
    isRecommended: option.isRecommended,

    // Info:: this field does not exist
    additionalDescription: '',
});

export default mapToShippingOption;
